<script>
export let user;
/**
 * Range should be:
 * - in YYYY-MM-DD format OR Date object
 * - an array with 2 items
 */
export let range = [];

const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

let start, end, container, calendar = [];

$: if (range.length == 2) {
    start = typeof range[0] == 'string' ? new Date(range[0]) : range[0];
    end = typeof range[1] == 'string' ? new Date(range[1]) : range[1];
}

$: if (start && end) {
    calendar = createCalendar();
}

function createCalendar()
{
    let dates = [];
    for (let month = start.getMonth(); month <= end.getMonth(); month++) {
        let obj = {
            month,
            weeks: []
        };

        let current = month == start.getMonth() ? start : new Date(start.getFullYear(), month, 1),
            ldom = new Date(current.getFullYear(), month + 1, 0),
            endCompare = month == end.getMonth() ? end : ldom,
            weeks = [], week = createEmptyWeek();

        while (current <= endCompare) {
            week[current.getDay()] = current;

            if (current.getDay() == 6) {
                weeks.push(week);
                week = createEmptyWeek();
            }

            current = new Date(current.getFullYear(), current.getMonth(), current.getDate() + 1);
            
            if (current > ldom || current > end) {
                weeks.push(week);
            }
        }

        obj.weeks = weeks;
        dates.push(obj);
    }
    
    return dates;
}

function createEmptyWeek()
{
    return [false, false, false, false, false, false, false];
}
</script>

<h1 class="text-center mt-5">
    {start.getDate()} {monthNames[start.getMonth()]} {start.getFullYear()} 
    &#8212; 
    {end.getDate()} {monthNames[end.getMonth()]} {end.getFullYear()}
</h1>

<div class="calendar" bind:this={container}>
    {#each calendar as {month, weeks}}
        <h3 class="month">{monthNames[month]}</h3>

        <div class="d-flex justify-content-end header">
            <div class="column weekend">
                SUN
            </div>

            <div class="column">
                MON
            </div>

            <div class="column">
                TUE
            </div>

            <div class="column">
                WED
            </div>

            <div class="column">
                THU
            </div>

            <div class="column">
                FRI
            </div>

            <div class="column weekend">
                SAT
            </div>
        </div>
        
        {#each weeks as week}
            <div class="d-flex just-content-end body">
                {#each week as day}
                    {#if day == false}
                        <div class="column no-day"></div>
                    {:else}
                        <div class="column day">
                            {day.getDate()}
                        </div>
                    {/if}
                {/each}
            </div>
        {/each}
    {/each}
</div>

<style>
.calendar {
    padding: .5rem;
    border-radius: .355rem;
    margin-top: 2rem;
    box-shadow: 0 0 0 1px rgba(43, 45, 80, 0.1),
        0 2px 5px 0 rgba(43, 45, 80, 0.08),
        0 1px 1.5px 0 rgba(0, 0, 0, 0.07),
        0 1px 2px 0 rgba(0, 0, 0, 0.08);
}

.calendar .column {
    width: 14.285714285714286%;
    text-align: center;
}

.calendar .header {
    border-bottom: 1px solid #ececec;
    padding-bottom: .5rem;
    padding-top: .5rem;
}

.calendar .header .column {
    font-size: 1.4rem;
}

.calendar .header .weekend {
    color: #f33;
}

.calendar .body .column {
    padding: 2rem 0;
}

.calendar .body .day {
    color: #ececec;
    font-size: 1.5rem;
}

.calendar .body .no-day {
    background: #ececec;
    height: 100px;
}
.calendar .month {
    background: #ececec;
    padding: 1rem 0;
    text-align: center;
}
</style>